* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #111;
  color: #fff;
  text-align: center;
  font-family: sans-serif;
}

h1 {
  margin-top: 50px;
  margin-bottom: 50px;
  font-size: 50px;
  text-transform: uppercase;
  font-weight: bold;
}

.form {
  width: 600px;
  margin: auto;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

input {
  width: 100%;
  height: 30px;
  margin: auto;
  display: block;
  margin-bottom: 20px;
  padding: 15px 10px;
  border-radius: 4px;
  border: none;
  border-bottom: 3px solid rgba(61, 121, 231, 0.932);
  background: none;
  caret-color: #03a9f4cc;
  font-size: 15px;
  color: white;
}

input:focus {
  outline: 0;
}

input::placeholder {
  color: rgba(255, 255, 255, 0.61);
}

button.add {
  display: block;
  width: 250px;
  padding: 10px 15px;
  border-radius: 4px;
  background: #03a9f4cc;
  border: 2px solid #03a9f4cc;
  color: white;
  font-weight: bold;
  margin-top: 15px;
  cursor: pointer;
}

.posts {
  width: 600px;
  margin: auto;
  margin-top: 60px;
  background: none;
  border-radius: 5px;
}

.post {
  width: 100%;
  background-color: #03a9f4cc;
  padding: 30px 20px;
  margin-bottom: 20px;
  text-align: left;
  border-radius: 5px;
}

h2 {
  margin-bottom: 10px;
}

h2 + p {
  margin: 20px 0;
}
.buttons {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 10px;
}
.post-btn {
  display: block;
  width: 250px;
  padding: 10px 15px;
  border-radius: 4px;
  background: #009688;
  border: 2px solid #009688;
  color: white;
  font-weight: bold;
  margin-top: 15px;
  cursor: pointer;
  border-radius: 4px;
}

.del {
  background: #de1258;
  border: 2px solid #de1258;
}
